@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

.App{
  width: 100%;
  height: 100vh;
  padding: 25px 150px;
  background-color: #f7f9fd;
}

.top_main{
width: 100%;
height: 250px;
/* border: 1px solid black; */
display: flex;
justify-content: space-between;
}

.budget_main, .expense_main{
  background: #fff;
  padding: 1.25em 0.9em;
  border-radius: 0.3em;
  box-shadow: 0 0.6em 1.2em rgba(28, 0, 80, 0.06);
  width: 600px;
  height: 100%;
}

.budget_submit, .expense_submit{
  font-size: 0.9em;
  margin-top: 0.6em;
  background-color: #587ef4;
  border: none;
  outline: none;
  color: #fff;
  padding: 0.4em 1em;
  border-radius: 0.3em;
  cursor: pointer;
}

h3{
  color: #363d55;
  font-weight: 500;
  font-size: 2rem;
  margin-bottom: 0.6em;
}

.error{
  color: #ff465a;
}
.hide{
  display: none;
}

.budget_input, .expense_input{
  display: block;
  width: 100%;
  padding: 0.6em 0.3em;
  border: 1px solid #d0d0d0;
  border-radius: 0.3em;
  color: #414a67;
  outline: none;
  font-weight: 400;
  margin-bottom: 0.6em;
}

.budget_input:focus{
  border-color: #587ef4;
}


.output-container{
  background-color: #587ef4;
  color: #fff;
  border-radius: 0.3em;
  box-shadow: 0 0.6em 1.2em rgba(28, 0, 80, 0.06);
  margin: 2em 0;
  padding: 1.2em;
}

.output-container p{
  font-weight: 500;
  margin-bottom: 0.6em;
}

.output-container span{
  display: block;
  text-align: center;
  font-weight: 400;
  color: #e5e5e5;
}

.flex-space{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.list{
  background-color: #fff;
  padding: 1.8em 1.2em;
  box-shadow: 0 0.6em 1.2em rgba(28, 0, 80, 0.06);
  border-radius: 0.6em;
}

.item_btn{
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 1.3rem;
  color: #587ef4;
}


.sublist-content{
  width: 100%;
  border-left: 0.3em solid #587ef4;
  margin-bottom: 0.6em;
  padding: 0.5em 1em;
  display: grid;
  background-color: #f7f9fd;
  grid-template-columns: 3fr 2fr 1fr 1fr;
}